import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";

function App() {
  const [list, setList] = useState([]);

  function addListItem(newValue) {
    setList((prev) => [...prev, newValue]);
  }

  function deleteListItem(newValue) {
    setList((prev) =>
      prev.filter((val) => {
        return newValue != val;
      })
    );
  }

  return (
    <div>
      <Header />
      <CreateArea func={addListItem} />
      {list.map((note, index) => {
        return <Note func={deleteListItem} key={index} noteVal={note} />;
      })}
      <Footer />
    </div>
  );
}

export default App;
